<template>
    <div class="datamanager">
      <!--  -->
      <div class="toolbox" @click="toggleToolbox">
        <img :src="toolboxIcon" alt="Toolbox Icon" class="toolbox-icon">
        <span class="toolbox-text"></span>
      </div>
      <!--  -->
      <div v-if="showToolboxPopup" class="toolbox-popup">
        <div class="toolbox-grid">
          <div class="toolbox-item" @click="openLoadDataPopup">
            <img src="@/assets/image/gis_dev_zgw_img/add_data.png" alt="">
          </div>
          <div class="toolbox-item" @click="toggleTimeSlider"  :class="{ active: !timeSliderDisabled }">
            <img src="@/assets/image/gis_dev_zgw_img/time_slider.png" alt="">
          </div>
          <div class="toolbox-item">
            <img src="@/assets/image/gis_dev_zgw_img/filter.png" alt="">
          </div>
          <div class="toolbox-item">
            <img src="@/assets/image/gis_dev_zgw_img/deal_data.png" alt="">
          </div>
          <div class="toolbox-item">
            <img src="@/assets/image/gis_dev_zgw_img/dot_density.png" alt="">
          </div>
          <div class="toolbox-item">
            <img src="@/assets/image/gis_dev_zgw_img/select.png" alt="">
          </div>
        </div>
        <!--  -->
        <span class="toolbox-close" @click="toggleToolbox">
          <img src="@/assets/image/map_icon/cancel_dark.png" alt="">
        </span>
      </div>
      <!--  -->
      <div v-if="showLoadDataPopup" class="modal-overlay">
        <div class="modal-content">
          <h2></h2>
          <div>
            <label for="dataSelect"></label>
            <select id="dataSelect">
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
            </select>
          </div>
          <div class="modal-buttons">
            <button @click="confirmLoadData"></button>
            <button @click="cancelLoadData"></button>
          </div>
        </div>
      </div>
      <!--  -->
      <div id="viewDiv"></div>
      <div id="timeSliderDiv" class="time-slider" :style="{ maxHeight: showTimeSlider ? '30%' : '0%' }"></div>
    </div>
</template>

<script>
import { markRaw } from 'vue'
import Map from '@geoscene/core/Map.js'
import MapView from '@geoscene/core/views/MapView.js'
import SpatialReference from '@geoscene/core/geometry/SpatialReference.js'
import FeatureLayer from '@geoscene/core/layers/FeatureLayer.js'
import TileInfo from '@geoscene/core/layers/support/TileInfo.js'
import BasemapGallery from '@geoscene/core/widgets/BasemapGallery.js'
import Compass from '@geoscene/core/widgets/Compass.js'
// import { handleKeydown, updateTime, clc1, clc2, isDateDisabled, handleDateChange, onTimeInputChange, onSearchInputChange } from '@/assets/share_js/routeplanning_all'
import ScaleBar from '@geoscene/core/widgets/ScaleBar.js'
import DistanceMeasurement2D from '@geoscene/core/widgets/DistanceMeasurement2D.js'
import LayerList from '@geoscene/core/widgets/LayerList'
import TimeSlider from '@geoscene/core/widgets/TimeSlider.js' //  TimeSlider

export default {
  name: 'DatamanagerView',
  data () {
    return {
      // data
      // resultLayer: markRaw(null),
      resultLayer: null, //  resultLayer  null markRaw(null)
      toolboxIcon: require('@/assets/image/gis_dev_zgw_img/Toolbox_4868.png'), // 
      BasemapName: '',
      ismaploading: true,

      showToolboxPopup: false, // 
      showLoadDataPopup: false, // 
      showTimeSlider: false, // 
      timeSliderDisabled: true, // 
      timeSlider: null //  TimeSlider 
      // timeSlider: markRaw(null)
    }
  },
  watch: {
    // watch
    timeSliderDisabled (newVal) {
      if (this.timeSlider) {
        this.timeSlider.disabled = newVal
        this.timeSlider.renderNow() //  UI 
      }
    }
  },
  methods: {
    // methods
    // 
    toggleToolbox () {
      this.showToolboxPopup = !this.showToolboxPopup
    },
    toggleTimeSlider () {
      this.timeSliderDisabled = !this.timeSliderDisabled
      this.showTimeSlider = !this.showTimeSlider
      if (this.timeSlider) {
        this.timeSlider.disabled = this.timeSliderDisabled
        this.timeSlider.renderNow() // 
      }
    },
    openLoadDataPopup () {
      this.showLoadDataPopup = true
    },
    confirmLoadData () {
      // TODO: 
      this.showLoadDataPopup = false
    },
    cancelLoadData () {
      this.showLoadDataPopup = false
    },
    // 
    initMap () {
      const map = new Map({
        basemap: 'tianditu-vector'
      })
      const spatialReference = new SpatialReference({
        wkid: 4326
      })
      const tileInfo = TileInfo.create({
        spatialReference,
        numLODs: 32
      })
      this.createMapView(map, tileInfo)
    },
    // 
    createMapView (map, tileInfo) {
      //  FeatureLayer 
      const featureLayer = new FeatureLayer({
        url: 'https://www.geosceneonline.cn/server/rest/services/Hosted/wuhan_village/FeatureServer',
        title: '',
        renderer: {
          type: 'simple', // 
          symbol: {
            type: 'simple-fill', // 
            color: [0, 0, 0, 0], // 
            outline: {
              color: [0, 0, 0, 1], // 
              width: 1
            }
          }
        },
        popupEnabled: false // 
        // popupTemplate: {
        //   content: [{
        //     type: 'fields',
        //     fieldInfos: [{
        //       fieldName: 'name',
        //       label: ''
        //     }]
        //   }]
        // }
      })
      //  markRaw  resultLayer
      this.resultLayer = markRaw(new FeatureLayer({
        url: 'https://www.geosceneonline.cn/server/rest/services/Hosted/result_2024_12_15_10min/FeatureServer',
        title: '',
        renderer: {
          type: 'class-breaks', // 
          field: 'result', //  'result' 
          classBreakInfos: [
            {
              minValue: 0,
              maxValue: 10,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#00FF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '0 - 10'
            },
            {
              minValue: 10,
              maxValue: 50,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFFF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '10 - 50'
            },
            {
              minValue: 50,
              maxValue: 100,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFA500', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '50 - 100'
            },
            {
              minValue: 100,
              maxValue: Infinity,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FF0000', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '> 100'
            }
          ]
        },
        popupTemplate: {
          title: '{road_name} - {result}', //  road_name  result
          content: [{
            type: 'fields',
            fieldInfos: [
              { fieldName: 'pid', label: 'PID' },
              { fieldName: 't07:20:00', label: '07:20:00' },
              { fieldName: 't07:30:00', label: '07:30:00' },
              { fieldName: 't07:40:00', label: '07:40:00' },
              { fieldName: 't07:50:00', label: '07:50:00' },
              { fieldName: 't08:00:00', label: '08:00:00' },
              { fieldName: 't08:10:00', label: '08:10:00' },
              { fieldName: 't08:20:00', label: '08:20:00' },
              { fieldName: 't08:30:00', label: '08:30:00' },
              { fieldName: 't08:40:00', label: '08:40:00' },
              { fieldName: 't08:50:00', label: '08:50:00' },
              { fieldName: 't09:00:00', label: '09:00:00' },
              { fieldName: 't09:10:00', label: '09:10:00' },
              { fieldName: 't09:20:00', label: '09:20:00' },
              { fieldName: 't09:30:00', label: '09:30:00' },
              { fieldName: 't09:40:00', label: '09:40:00' },
              { fieldName: 't09:50:00', label: '09:50:00' },
              { fieldName: 't14:50:00', label: '14:50:00' },
              { fieldName: 't15:00:00', label: '15:00:00' },
              { fieldName: 't15:10:00', label: '15:10:00' },
              { fieldName: 't15:20:00', label: '15:20:00' },
              { fieldName: 't15:30:00', label: '15:30:00' },
              { fieldName: 't15:40:00', label: '15:40:00' },
              { fieldName: 't15:50:00', label: '15:50:00' },
              { fieldName: 't16:00:00', label: '16:00:00' },
              { fieldName: 't16:10:00', label: '16:10:00' },
              { fieldName: 't16:20:00', label: '16:20:00' },
              { fieldName: 't16:30:00', label: '16:30:00' },
              { fieldName: 't16:40:00', label: '16:40:00' },
              { fieldName: 't16:50:00', label: '16:50:00' },
              { fieldName: 't17:00:00', label: '17:00:00' },
              { fieldName: 't17:10:00', label: '17:10:00' },
              { fieldName: 't17:20:00', label: '17:20:00' },
              { fieldName: 'result', label: '' },
              { fieldName: '50lon', label: '' },
              { fieldName: '50lat', label: '' },
              { fieldName: 'near_fid', label: 'FID' },
              { fieldName: 'yaw', label: '' }
            ]
          }]
        }
      }))

      //  FeatureLayer 
      map.add(this.resultLayer)
      map.add(featureLayer)

      //  MapView 
      const mapView = new MapView({
        map: map,
        center: [114.3, 30.7], // 
        zoom: 4,
        container: 'viewDiv',
        constraints: {
          geometry: {
            type: 'extent',
            xmin: 113.6,
            ymin: 29.9,
            xmax: 115.0,
            ymax: 31.3
          },
          minScale: 500,
          maxScale: 2000000,
          rotationEnabled: false,
          lods: tileInfo.lods,
          snapToZoom: false
        }
      })
      //  `when`  `ismaploading`  false
      mapView.when(() => {
        this.ismaploading = false
      })
      //  BasemapGallery 
      const basemapGallery = new BasemapGallery({
        view: mapView,
        source: {
          query: {
            title: '"Basemaps for Developers" AND owner:geoscenedev'
          }
        }
      })
      // 
      basemapGallery.watch('activeBasemap', (newBasemap) => {
        this.handleBasemapChange(newBasemap)
      })
      //  LayerList 
      const layerList = new LayerList({
        view: mapView
      })
      //  Compass 
      const compass = new Compass({
        view: mapView
      })
      //  ScaleBar 
      const scaleBar = new ScaleBar({
        view: mapView,
        unit: 'metric', // 
        style: 'ruler' // 
      })
      //  DistanceMeasurement2D 
      const distanceMeasurement2D = new DistanceMeasurement2D({
        view: mapView,
        unit: 'metric',
        unitOptions: {
          metric: ['kilometers', 'meters'],
          nonMetric: ['miles', 'feet']
        },
        iconClass: 'esri-icon-measure-line' // 
      })
      //  BasemapToggle 
      // const basemapToggle = new BasemapToggle({
      //   view: mapView,
      //   nextBasemap: 'tianditu-image',
      //   basemap: 'tianditu-vector'
      // })
      // mapView.ui.add(basemapToggle, 'bottom-right')
      //  BasemapGallery 
      mapView.ui.add(basemapGallery, {
        position: 'bottom-right',
        index: 0
      })
      //  LayerList 
      mapView.ui.add(layerList, {
        position: 'bottom-right',
        index: 1
      })
      //  DistanceMeasurement2D 
      mapView.ui.add(distanceMeasurement2D, {
        position: 'bottom-leading',
        index: 0 // 
      })
      // 
      mapView.ui.move('zoom', {
        position: 'bottom-left',
        index: 1
      })
      // 
      mapView.ui.add(compass, {
        position: 'bottom-left',
        index: 2
      })
      //  ScaleBar 
      mapView.ui.add(scaleBar, {
        position: 'bottom-left',
        index: 3
      })

      //  TimeSlider 
      const timeSlider = markRaw(new TimeSlider({
        title: '',
        container: 'timeSliderDiv',
        view: mapView,
        disabled: this.timeSliderDisabled,
        mode: 'instant',
        layout: 'compact',
        fullTimeExtent: {
          start: new Date(2024, 11, 15, 7, 20, 0),
          end: new Date(2024, 11, 15, 17, 20, 0)
        },
        timeExtent: {
          start: new Date(2024, 11, 15, 7, 20, 0),
          end: new Date(2024, 11, 15, 7, 20, 0)
        },
        stops: {
          interval: {
            unit: 'minutes',
            value: 10
          }
        }
      }))
      this.timeSlider = timeSlider // 
      mapView.ui.add(timeSlider, 'bottom-left')

      //  `timeSlider.watch` 
      //   timeSlider.watch('timeExtent', async (value) => {
      //     const selectedTime = value.start
      //     const hours = selectedTime.getHours().toString().padStart(2, '0')
      //     const minutes = selectedTime.getMinutes().toString().padStart(2, '0')
      //     const fieldName = `t${hours}:${minutes}:00`

      //     console.log(`Selected Time: ${selectedTime}`)
      //     console.log(`Field Name: ${fieldName}`)

      //     // 1
      //     resultLayer.definitionExpression = `"${fieldName}" = 1`
      //     console.log(`Updated definitionExpression: ${resultLayer.definitionExpression}`)

      //     // 
      //     try {
      //       await resultLayer.refresh()
      //       console.log('')
      //     } catch (error) {
      //       console.error(':', error)
      //     }
      //   })

      //  `timeSlider.watch` 
      timeSlider.watch('timeExtent', async (value) => {
        const selectedTime = value.start
        const hours = selectedTime.getHours().toString().padStart(2, '0')
        const minutes = selectedTime.getMinutes().toString().padStart(2, '0')
        const fieldName = `t${hours}:${minutes}:00`

        console.log(`Selected Time: ${selectedTime}`)
        console.log(`Field Name: ${fieldName}`)

        if (this.resultLayer) {
          // 
          this.resultLayer.renderer = {
            type: 'unique-value',
            field: fieldName,
            uniqueValueInfos: [
              {
                value: 1,
                symbol: {
                  type: 'simple-marker',
                  style: 'circle',
                  color: '#149ece',
                  size: 6,
                  outline: {
                    color: 'white',
                    width: 1
                  }
                },
                label: ''
              },
              {
                value: 2,
                symbol: {
                  type: 'simple-marker',
                  style: 'circle',
                  color: 'transparent',
                  size: 6,
                  outline: {
                    color: 'transparent',
                    width: 1
                  }
                },
                label: ''
              }
            ],
            defaultSymbol: {
              type: 'simple-marker',
              style: 'circle',
              color: 'transparent',
              size: 6,
              outline: {
                color: 'transparent',
                width: 1
              }
            }
          }

          console.log(`Updated renderer based on field: ${fieldName}`)
          // 
          try {
            await this.resultLayer.refresh()
            console.log('')
          } catch (error) {
            console.error(':', error)
          }
        } else {
          console.error('resultLayer ')
        }
      })
      this.mapView = mapView
      const BasemapName = this.mapView.map.basemap.title
      console.log('BasemapName old:', BasemapName)
      // BasemapName 
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }
      this.BasemapName = basemapMapping[BasemapName] || BasemapName
      console.log('BasemapName changed in rpv:', this.BasemapName)
      return mapView
    },
    // 
    handleBasemapChange (basemap) {
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }
      //  basemap.title 
      if (basemapMapping[basemap.title]) {
        this.BasemapName = basemapMapping[basemap.title]
      } else {
        this.BasemapName = basemap.title
      }
      console.log('Basemap changed:', this.BasemapName)
      const urlParams = new URLSearchParams(window.location.search)
      urlParams.set('BasemapLayer', this.BasemapName)
      window.history.replaceState({}, '', `${window.location.pathname}?${urlParams}`)
    },
    created () {
      const BasemapLayer = this.$route.query.BasemapLayer
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }

      // 
      if (BasemapLayer) {
        //  BasemapLayer 
        if (basemapMapping[BasemapLayer]) {
          this.BasemapName = basemapMapping[BasemapLayer]
        } else {
          this.BasemapName = BasemapLayer
        }
      }
      console.log('BasemapLayer is', this.BasemapName)
    }
  },
  mounted () {
    // mounted
    this.initMap()
  }

}
</script>

<style>
#viewDiv {
  position: absolute; /*  */
  top: 0; /*  */
  bottom: 0;
  left: 0;
  right: 0;
  height:100%; /* 100% */
  width: 100%; /* 100% */
  z-index: -1; /* z-indexApp.vue */
  margin: auto;
}

.toolbox {
  position: absolute;
  top: 1.4%;
  left: 5px;
  height: 5.7%;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  z-index: 1000; /*  */

  background-color: rgba(255, 255, 255, 0.5) !important; /*  */
  -webkit-backdrop-filter: blur(25px) !important; /*  */
  backdrop-filter: blur(25px) !important; /*  */
  border: none !important;
  box-shadow: none !important;
  border: 1px solid rgba(222, 222, 222, 0.45); /*  */
  color: rgb(109, 72, 72);
}

.toolbox-item.active {
  background-color: red; /*  */
}

.toolbox-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 */
  grid-template-rows: repeat(2, 1fr);    /* 2 */
  gap: 10px;                             /*  */
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(25px);
  padding: 10px;
  border-radius: 10px;
}

.toolbox-icon {
  width: 5vh; /* 5vh */
  height: auto; /*  */
  margin-right: 5px;
}

.toolbox-text {
  font-size: 16px;
  font-weight: bold;
}

.toolbox-popup {
  position: absolute;
  top: 8vh; /*  */
  left: 10px;
  width: flex; /*  */
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*  */
  z-index: 1000; /*  */

  background-color: rgba(255, 255, 255, 0.5) !important; /*  */
  -webkit-backdrop-filter: blur(25px) !important; /*  */
  backdrop-filter: blur(25px) !important; /*  */
  border: none !important;
  /* box-shadow: none !important; */
  border: 1px solid rgba(222, 222, 222, 0.45); /*  */
}

.toolbox-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 */
  grid-template-rows: repeat(2, 1fr);    /* 2 */
  gap: 10px;                             /*  */
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(25px);
  padding: 10px;
  border-radius: 10px;
}

.toolbox-item {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: antiquewhite; */
  background-color: rgb(216, 180, 133);
  /* background-color: rgba(255, 255, 255, 0.5); */
  color: rgb(109, 72, 72);
  border: 1px solid rgba(222, 222, 222, 0.45);
  cursor: pointer;
  border-radius: 5px;
  /* height: 60px; */
  aspect-ratio: 1 / 1; /*  */
  white-space: normal; /*  */
  word-break: break-all; /*  */
  text-align: center; /*  */
  max-width: 4ch; /*  */
  /* padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px; */
  padding: 5px;
  transition: background-color 0.3s;
}

.toolbox-item img {
  width: 3vh; /* 40px */
  height: auto;
}

.toolbox-item:hover {
  background-color: rgb(253, 185, 97);
}

/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /*  */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/*  */
.modal-content {
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(25px);
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  color: rgb(109, 72, 72);
  border: 1px solid rgba(222, 222, 222, 0.45);
}

.modal-content h2 {
  margin-top: 0;
}

.modal-buttons {
  margin-top: 20px;
  text-align: right;
}

.modal-buttons button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: antiquewhite;
  border: 1px solid rgba(222, 222, 222, 0.45);
  border-radius: 5px;
  color: rgb(109, 72, 72);
  cursor: pointer;
}

.modal-buttons button:hover {
  background-color: rgb(216, 180, 133);
}

.toolbox-close {
  display: flex;
  justify-content: center; /*  */
  margin-top: 10px;       /*  */
}

.toolbox-close img {
  cursor: pointer;
}

.time-slider {
  position: fixed;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 10px; /*  */
}

/*  */
.geoscene-distance-measurement-2d__clear-button{
  background-color: antiquewhite;
  border-left-color: antiquewhite;
  border-right-color: antiquewhite;
  border-top-color: antiquewhite;
  border-bottom-color: antiquewhite;
  color:rgb(109, 72, 72);
  /* background-color: rgba(255, 255, 255, 0.5);  */
  -webkit-backdrop-filter: blur(25px); /*  */
  backdrop-filter: blur(25px); /*  */
}

.geoscene-distance-measurement-2d__clear-button:hover{
  background-color: rgb(216, 180, 133);
  border-left-color: rgb(216, 180, 133);
  border-right-color: rgb(216, 180, 133);
  border-top-color: rgb(216, 180, 133);
  border-bottom-color: rgb(216, 180, 133);
  color: rgb(109, 72, 72);
  -webkit-backdrop-filter: blur(25px); /*  */
  backdrop-filter: blur(25px); /*  */
}

.geoscene-distance-measurement-2d__actions{
  background-color: transparent;
}

